import Vue from 'vue';
import App from './App';
import vuetify from './plugins/vuetify';
import router from './router';
import axios from 'axios';
Vue.config.productionTip = false;
Vue.prototype.$axios = axios;
new Vue({
    router,
    vuetify,
    components: { App },
    render: (h) => h(App),
}).$mount('#app');
